<template>
  <div class="wrap-box">
    <div class="editor-box">
      <v-editor :config=config id="ue1" ref="ueditor"></v-editor>
    </div>
    <button @click="getContent()"></button>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        config: {
          autoHeightEnabled: false,
          autoFloatEnabled: true,//
          initialContent: '',   //,textarea/script
          autoClearinitialContent: false, //focustrue,
          initialFrameWidth: null,
          initialFrameHeight: 500,
          BaseUrl: '',
          UEDITOR_HOME_URL: process.env.NODE_ENV == 'development' ? '/static/ueditor/' : '/ueditor-uploadimage-qinniu/dist/static/ueditor/',
        },
      };
    },
    mounted(){
      this.config.initialContent = '...  ';
    },
    methods: {
      getContent(){
        alert(UE.getEditor('ue1').getContent())
      }
    }
  }

</script>

<style lang="scss" scoped>
  @import '../assets/css/mixin';
  .wrap-box{
    margin: 30px auto;
    width: 800px;
    height: 500px;
  }
  .editor-box {
    margin-bottom: 20px;
  }
</style>



